.toggle-menu {
  position: relative;
  float: right;
  width: 32px;
  height: 32px;
  display: none;
  z-index: 100;
  border: none;
  background-color: transparent;
}

@media only screen and (max-width: 1023px) {
  .toggle-menu {
    display: block;
  }
}

.toggle-menu:before {
  content: "";
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}

