[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
}

[data-modal]:after {
  content: " ";
  display: table;
  clear: both;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
  font-size: 14px;
  float: right;
}

[data-modal-close] {
  position: absolute;
  background-color: transparent;
  font-size: 0;
}

[data-modal-close] .icon {
  font-size: 16px;
}

